.yt_container {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  width: auto;
  margin: 1.5rem;
}

.yt_content {
  margin: auto;
  border-radius: 0.5rem;
  margin-bottom: 2rem;
  /* border: 1px solid var(--text-secondary); */
  object-fit: contain;
  box-shadow: rgba(50, 50, 93, 0.25) 0px 50px 100px -20px,
    rgba(109, 109, 109, 0.3) 0px 30px 60px -30px,
    rgba(10, 37, 64, 0.35) 0px -2px 6px 0px inset;
}

.yt {
  margin-left: 2rem;
}

.yt_hr {
  width: 97%;
}
h3 {
}

.yt_img > img {
  width: auto;
  height: 12rem;
  border-radius: 0.5rem 0.5rem 0 0;
}

@media (max-width: 980px) {
  .yt_container {
    display: grid;
    grid-template-columns: repeat(1, 1fr);
  }
  .yt_content {
    margin-top: 2rem;
  }
  .yt_img > img {
  }
}

@media (max-width: 480px) {
  .yt_container {
    display: grid;
    grid-template-columns: repeat(1, 1fr);
    margin: auto;
  }

  .yt_img > img {
    width: 100%;
  }
}
